<!--  -->
<template>
<div class='createBlog'>
  <div class='header'><span></span></div>
  <el-form class="el_form" :model="blog" ref="blog" :rules="rules" label-width="100px">
    <el-form-item label="" prop="title">
      <el-input class="input" v-model="blog.title" placeholder="" clearable></el-input>
    </el-form-item>
    <el-form-item label="" prop="tag">
      <el-select v-model="blog.tag"  placeholder="">
        <el-option
        v-for="item in tagLists"
        :key="item.label_id"
        :label="item.label_name"
        :value="item.label_id">
        </el-option>
      </el-select>
      <el-button icon="el-icon-plus" size="small" class="new_tag" type="primary" @click=" centerDialogVisible = true"></el-button>

    </el-form-item>
    <el-form-item label="" prop="abstract">
      <el-input
        type="textarea"
        :autosize="{ minRows: 2}"
        placeholder=""
        v-model="blog.abstract">
      </el-input>
    </el-form-item>

    <el-form-item label="" prop="picture">
      <el-upload
        action=''
        :auto-upload="true"
        :http-request="upload"
        list-type="picture-card"
        :show-file-list="false"
        :before-upload="beforeAvatarUpload">
        <i v-if="!blog.picture" class="el-icon-plus"></i>
        <img v-if="blog.picture" style="width: 100%" :src="blog.picture" alt="">
      </el-upload>
    </el-form-item>

    <el-form-item label="" prop="content">
      <mavon-editor class="markdown" v-model="blog.content" :toolbars="toolbars"></mavon-editor>
    </el-form-item>
    <el-form-item class="button">
      <el-button type="primary" @click="submitForm('blog')"></el-button>
      <el-button @click="resetForm('blog')"></el-button>
    </el-form-item>
  </el-form>
  <el-dialog
  title=""
  :visible.sync="centerDialogVisible"
  width="30%"
  center>
  <el-input v-model="newTag" placeholder=""></el-input>
  <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false"> </el-button>
    <el-button type="primary" @click="createTag"> </el-button>
  </span>
</el-dialog>
</div>
</template>

<script>
//jsjsjson
//import  from '';
import {postBlogData ,getTags, newTag} from 'network/home.js'
import {changeFileName, blogBucket} from 'utils/alioss.js'
export default {
//import
components: {
  
},
computed: {
  tagArray() {
    let tags = []
    for (let item of this.tagLists) {
      tags.push(item.label_name.toLowerCase())
    }
    return tags
  }
},
data() {
//
return {
  dialogImageUrl: '',
  dialogVisible: false,



  blog: {
    title: '',
    tag: '',
    content: '',
    abstract: '',
    picture: ''
  },

  centerDialogVisible: false,
  newTag: '',

  tagLists: [],
  //
  rules: {
    title: [
      {required: true, message: '', type: 'string', trigger: 'blur'},
      {max: 25, message: '25', trigger: 'blur'}
    ],
    tag: [
      {required: true, message:"", trigger: 'change'}
    ],
    content: [
      {required: true, message: '', type: 'string', trigger: 'change'}
    ],
    abstract: [
      {required: true, message: '', type: 'string', trigger: 'change'}
    ],
    picture: [
      {required: true, message: '', type: 'string', trigger: 'change'}
    ]
  },
  //markdown
  toolbars:{
          bold: true, // 
          italic: true, // 
          header: true, // 
          underline: true, // 
          strikethrough: true, // 
          mark: true, // 
          superscript: true, // 
          subscript: true, // 
          quote: true, // 
          ol: true, // 
          ul: true, // 
          link: true, // 
          imagelink: true, // 
          code: true, // code
          table: true, // 
          fullscreen: true, // 
          readmodel: true, // 
          htmlcode: true, // html
          help: true, // 
          /* 1.3.5 */
          undo: true, // 
          redo: true, // 
          trash: false, // 
          save: false, // eventssave
          /* 1.4.2 */
          navigation: true, // 
          /* 2.1.8 */
          alignleft: true, // 
          aligncenter: true, // 
          alignright: true, // 
          /* 2.2.1 */
          subfield: true, // 
          preview: true, // 
        }


};
},
methods: {
  //
  submitForm(formName) {
    this.$refs[formName].validate((valid) => {
      if (valid) {
        postBlogData(this.blog.title, this.blog.tag, this.blog.content, this.blog.abstract, this.blog.picture).then(() => {
          this.$message.success('')
          this.resetForm('blog')
          document.documentElement.scrollTop = 0

        })
        
      } else {
        console.log('error submit!!');
        return false;
      }
    });
  },
  //
  resetForm(formName) {
    this.$refs[formName].resetFields();
  },
  //
  createTag() {
    
    if(this.tagArray.indexOf(this.newTag) > -1) {
      this.newTag = ''
      this.centerDialogVisible = false
      alert('')

    }else {
      newTag(this.newTag).then(result => {
        
        return getTags()
      
      }).then(result => {
        this.tagLists = result.data.data;
        
        this.newTag = ''
        this.centerDialogVisible = false
        alert('')

      })
      
    }


    
    
  },

  
  beforeAvatarUpload(file) {
    //
    let imgExg = /image\/*/i
    
    const isImg = imgExg.test(file.type)
    const isLt2M = file.size / 1024 / 1024 < 5

    if(!isImg) {
      this.$message.error('')
    }

    if (!isLt2M) {
      this.$message.error(' 5MB!');
    }

    return isImg && isLt2M

  },
  
  //element ui
  async upload(file) {
    //
    
    let filename = changeFileName(file.file.name);
    //blogPicture
    let res = await blogBucket().put(`blogPicture/${filename}`, file.file).catch(error => {
      throw(error)
    })
    this.$message.success('')
    this.blog.picture = res.url

  }
},
created() {
  //
  getTags().then(result => {
    this.tagLists = result.data.data;
  })
},
}


</script>

