import {request} from 'network/request.js'

//
export function getBlogDetails(id) {
    return request({
        method: 'get',
        url: '/api/detail',
        params: {
            id
        }
    })
}

//
export function postBlogData(title, tag, content, abstract, picture) {
    return request({
        method: 'post',
        url: '/api/newblog',
        data: {
            title,
            tag,
            content,
            abstract,
            picture
        }
    })
}

////
export function postPageData(pageSize, targetPage) {
    return request({
        method: 'post',
        url: '/api/blog',
        data: {
            pageSize,
            targetPage
        }
    })
}

